import React, { useState } from 'react'
import styled from 'styled-components';

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 100px 0;
  gap: 16px;
  @media screen and (max-width:400px) {
        padding: 0;
    }
`;

const H1 = styled.h1``;

const TopBox = styled.div`
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
    margin-bottom: 30px;
`
const Button = styled.button`
  font-weight: bold;
  background-color: #f6f3f2;
  padding: 0 13px;
  margin: 0 6px 6px 0;
  width: auto;
  height: 30px;
  border-radius: 15px;
  border: none;
  cursor: pointer;
  color: ${({$active})=>($active === "true" ? "#fff" : "#000")};
  background-color: ${({$active})=>($active === "true"? "#007fa8" : "#f6f3f2")};
`
const BottomBox = styled.div`
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
    @media screen and (max-width:1240px){
      gap: 2px;
    }
`
const ImgBox = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    cursor: pointer;
    position: relative;
    @media screen and (max-width:1240px){
      width: 49%;
    }
`
const HoverBox =styled.div`
   display: ${({$hover})=>($hover === "true" ? "flex" : "none")};
   flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,0.8);
  z-index: 100;

  width: 100%;
  height: 100%;

  position: absolute;
  padding: 10px;
`
const Sns = styled.span`
  position: relative;
  left: -100px;
  width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  padding: 5px;
  border: 2px solid #fff;
  border-radius: 25px;
  margin-left: 10px;
  font-weight: bold;
  @media screen and (max-width:1240px){
      left: -150px;
    }
`
const HoverText =styled.p`
  margin-top: 30px;
  color: #fff;
  text-align: center;
`
const Img = styled.img`
  width: 300px;
  object-fit: contain;
        
  @media screen and (max-width:1240px){
      width: 100%;
    }
`
const Icon = styled.img`
  margin-top: 30px;
  position: relative;
  right: -100px;
  @media screen and (max-width:1240px){
      right: -150px;
    }
`

