function News() {
  const [tag, setTag] = useState("#All")
  const [hoveredIndex, setHoveredIndex] = useState(null);
  return (
    <Wrapper>
        <H1>현대자동차 소식</H1>
        <TopBox>
          {kategorie.map((item)=>(
            <Button 
              key={item.id} 
              onClick={() => setTag(item.name)} 
              $active={(item.name === tag).toString()}
            >
              {item.name}
            </Button>
          ))}
        </TopBox>
        <BottomBox>
        
        {kategorie
            .find((k) => k.name === tag)
            ?.img.map((img, index) => (
              <ImgBox
              key={index}
              onMouseEnter={() => setHoveredIndex(index)}
              onMouseLeave={() => setHoveredIndex(null)}
              >
              <Img src={img} alt={`news ${index + 1}`} />
              <HoverBox
                $hover={hoveredIndex === index ? "true" : "false"}
              >
                <Sns>SNS</Sns>
                <HoverText>{kategorie.find((k) => k.name === tag).text[index]}</HoverText>
                <Icon src={kategorie.find((k) => k.name === tag).icon}></Icon>
              </HoverBox>
              </ImgBox>
            ))}
         
        </BottomBox>
    </Wrapper>
  )
}

export default News;
