import React, { useState } from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
    width: 100%;
    margin: 0 auto;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: transparent;
`;

const Container = styled.div`
    width: 100%;
    max-width: 700px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
    
    
`;

const Ul = styled.ul`
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    transition: transform 0.5s ease;
    transform: translateX(${(props) => props.translate}%);
    width: ${(props) => props.width}%;
    @media screen and (max-width:960px) {
        flex-wrap : wrap;
        width: 100%;
    }
`;

const Li = styled.li`
    flex: 1 0 16.6%; /* Flex basis is now 1/6th of the container */

    text-align: center;
`;

const A = styled.a`
    text-decoration: none;
    color: inherit;
`;

const Img = styled.img`
    width: 50px;
    height: 50px;
    @media screen and (max-width:960px) {
        width: 30px;
        height: 30px;
    }
`;

const P = styled.p`
    margin-top: 10px;
    font-size: 14px;
    font-weight: bold;

    @media screen and (max-width:960px) {
        font-size: 10px;
    }
`;

const Button = styled.button`
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
    &:disabled {
        cursor: not-allowed;
        opacity: 0.5;
    }
    &:focus {
        outline: none;
    }
    cursor: pointer;
    z-index: 10;

    @media screen and (max-width:960px) {
        display: none;
    }
    
`;

const PrevButton = styled(Button)`
  
`;

const NextButton = styled(Button)`
`;

const icons = [
    {
        id: 1,
        img: "assets/icon1.png",
        text: "견적내기"
    },
    {
        id: 2,
        img: "assets/icon2.png",
        text: "구매상담"
    },
    {
        id: 3,
        img: "assets/icon3.png",
        text: "시승신청"
    },
    {
        id: 4,
        img: "assets/icon4.png",
        text: "판매처 검색"
    },
    {
        id: 5,
        img: "assets/icon5.png",
        text: "구매해택"
    },
    {
        id: 6,
        img: "assets/icon6.png",
        text: "정비예약"
    },
    {
        id: 7,
        img: "assets/icon7.png",
        text: "블루멤버스"
    },
    {
        id: 8,
        img: "assets/icon8.png",
        text: "포인트사용"
    },
    {
        id: 9,
        img: "assets/icon9.png",
        text: "내비 업데이트"
    },
    {
        id: 10,
        img: "assets/icon10.png",
        text: "보증수리 안내"
    },
    {
        id: 11,
        img: "assets/icon11.png",
        text: "중고차"
    },
    {
        id: 12,
        img: "assets/icon12.png",
        text: "이벤트"
    }
];

const IconBox = () => {
    const [page, setPage] = useState(0);
    const itemsPerPage = 6;
    const totalPages = Math.ceil(icons.length / itemsPerPage);

    const handleNext = () => {
        if (page < totalPages - 1) {
            setPage(page + 1);
        }
    };

    const handlePrev = () => {
        if (page > 0) {
            setPage(page - 1);
        }
    };

    const translateValue = -(page * 100 / totalPages); 
    const containerWidth = totalPages * 100; 

    return (
        <Wrapper>
            <PrevButton onClick={handlePrev} disabled={page === 0}>&lt;</PrevButton>
            <Container>
                <Ul translate={translateValue} width={containerWidth}>
                    {icons.map((icon) => (
                        <Li key={icon.id}>
                            <A href="#">
                                <Img src={icon.img} alt={icon.text} />
                                <P>{icon.text}</P>
                            </A>
                        </Li>
                    ))}
                </Ul>
            </Container>
            <NextButton onClick={handleNext} disabled={page >= totalPages - 1}>&gt;</NextButton>
        </Wrapper>
    );
};

export default IconBox;
