import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;

    gap: 15px;
`
const Button = styled.button`
    width: 50px;
    height: 30px;
    background-color: transparent;

    cursor: pointer;
    @media screen and (max-width:960px) {
        display: none;
    }
`
const Bar = styled.div`
      @media screen and (max-width:960px) {
        display: none;
    }
`
const User = styled.div`
    width: 26px;
    height: 20px;

    @media screen and (max-width:960px) {
        display: none;
    }
`
const Search = styled.div`
    width: 26px;
    height: 20px;

`
const Menu = styled.div`
    width: 26px;
    height: 20px;
`

function Utill() {
  return (
    <Wrapper>
        <Button>KR</Button>
        <Bar>|</Bar>
        <User><img src="assets/user.svg" alt="유저" /></User>
        <Search><img src="assets/search.svg" alt="서치" /></Search>
        <Menu><img src="assets/bars.svg" alt="목록" /></Menu>
    </Wrapper>
  )
}

export default Utill