import React from 'react';
import { Navigation, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.min.css';
import styled from 'styled-components';
import IconBox from './IconBox';
import SwiperCore, { Autoplay,Pagination } from 'swiper';

SwiperCore.use([Autoplay,Pagination]);

const Wrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background-image: url("assets/car-bg-w.jpg");
    @media screen and (max-width:500px) {
        margin: 0 auto;
    }
`;

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    overflow: hidden;
`;

const Title = styled.p`
    font-size: 72px;
    font-weight: bold;
    z-index: 10;
    position: relative;
    top: 200px;
    left: 200px;
    @media screen and (max-width: 960px) {
        font-size: 24px;
        left: 0;
        top: 100px;
    }
`;

const Text = styled.p`
 position: relative;
    top: 200px;
    left: 200px;
    font-size: 26px;
    z-index: 10;
    @media screen and (max-width: 960px) {
        font-size: 18px;
        left: 0;
        top: 100px;
    }
`;

const ImgBox = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 auto;
    width: 90%;
    height: 100%;
    cursor: pointer;
    z-index: 100;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    
`;
const Img = styled.img`
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    height: 60%;
    @media screen and (max-width: 1240px) {
        height: 30%;
    }
    @media screen and (max-width: 500px) {
        height: 20%;
    }

`

const cars = [
    {
        id: 1,
        title: "ST1",
        text: "내일을 바꾸는 모빌리티",
        src: "assets/car1.png",
    },
    {
        id: 2,
        title: "The new IONIQ 5",
        text: "내가 선택한 단 하나의 전기차",
        src: "assets/car2.png",
    },
    {
        id: 3,
        title: "IONIQ6",
        text: "Black Edition",
        src: "assets/car3.png",
    },
    {
        id: 4,
        title: "The all-new GRANDEUR",
        text: "2024 국가대표브랜드 선정",
        src: "assets/car4.png",
    },
    {
        id: 5,
        title: "CASPER",
        text: "case by CASPER",
        src: "assets/car5.png",
    },
];

function Slider() {
    return (
        <div>
            <Wrapper>
                <Container>
                    <Swiper
                        modules={[Navigation, Pagination, A11y, Autoplay]}
                        spaceBetween={30}
                        slidesPerView={1}
                        style={{
                            "--swiper-pagination-color": "#007bff",
                            "--swiper-navigation-color": "#333",
                            
                            width : "100%",
                            height: "100%",
                        }}
                        pagination={{ clickable: true,
                         }}
                        loop={true}
                        autoplay={{
                            delay: 2500,
                            disableOnInteraction: false,
                        }}
                    >
                        {cars.map((car, index) => (
                            <SwiperSlide key={index} style={{ height: "100%" }}>
                               
                                <ImgBox>
                                <Title>{car.title}</Title>
                                <Text>{car.text}</Text>
                                <Img src={car.src}></Img>
                                </ImgBox>
                            </SwiperSlide>
                        ))}
                    </Swiper>
                    <IconBox />
                </Container>
            </Wrapper>
        </div>
    );
}

export default Slider;
