import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    padding-bottom: 30px;
    background-color: #1c1b1b;
    
`
const Container = styled.div`
    display: flex;
    align-items: flex-start;
    width: 1200px;
    @media screen and (max-width:760px){
        margin-top: 70px;
        border-top: 1px solid #333;
    }
`
const Ul = styled.ul`
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    @media screen and (max-width:760px){
        display: none;
    }
`
const Li = styled.li`
    display: flex;
    align-items: flex-start;
    list-style: none;
    color: #999;
    font-size: 12px;
    width: calc(100%/4);
    margin: 10px 0;
`
const Sapn = styled.span`
    margin: 0 10px;
`
const Img = styled.img`

`
const items = [
    {
        id : 1,
        img : "assets/footer1.png",
        text : "웹 접근성 품질인증"
    },
    {
        id : 2,
        img : "assets/footer2.png",
        text : "우수디자인 지식경제부선정"
    },
    {
        id : 3,
        img : "assets/footer3.png",
        text : "가족친화 우수기관"
    },
    {
        id : 4,
        img : "assets/footer4.png",
        text : "2023년 한국서비스품질지수 (KS-SQI) 자동차 A/S 부문 9년 연속 1위"
    },
    {
        id : 5,
        img : "assets/footer5.png",
        text : "2024년 국가고객만족도(NCSI) 조사대상 전 부문 1위 수상 (컴팩트승용, 중형, 준대형, 대형, 컴팩트RV, 대형RV, 전기차)"
    },
    {
        id : 6,
        img : "assets/footer6.png",
        text : "KMAC 선정 2023년 한국산업의 고객만족도 일반승용차 부문 30년 연속 RV승용차 부문 20년 연속 1위 경형승용차 부문 / 전기자동차 부문 1위"
    },
    {
        id : 7,
        img : "assets/footer7.png",
        text : "2023년 한국품질만족지수 (KS-QEI) 조사 10개 부문 1위 (승용 조사 대상 전 부문, SUV-소형/준중형/대형, 럭셔리세단 E세그, 대형럭셔리SUV, 전기차, 자동차AS)"
    }
]
function BottomFooter() {
  return (
    <Wrapper>
        <Container>
            <Ul>
                {items.map((item)=>(
                    <Li key={item.id}><Img src={item.img}></Img><Sapn>{item.text}</Sapn></Li>
                ))}
            </Ul>
        </Container>
    </Wrapper>
  )
}

export default BottomFooter