import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 100px 0;
    gap: 16px;
    @media screen and (max-width:1024px) {
       margin-top: 300px;
    }
    @media screen and (max-width:500px) {
        margin-top: 150px;
    }
`
const H1 = styled.h1`

`
const P = styled.p`
    padding: 30px 0 50px 0;
    color: #777;
    @media screen and (max-width:400px) {
       text-align: center;
    }
`
const Container =styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    
`
const TopBox = styled.div`
    display: flex;
    justify-content: space-between;

    gap: 16px;
    @media screen and (max-width:1024px) {
      flex-direction: column;
    }
`
const TopItems = styled.div`
    width: 350px;
    @media screen and (max-width:1024px) {
      width: 800px;
    }
    @media screen and (max-width:800px) {
      width: 500px;
    }
    @media screen and (max-width:500px) {
      width: 350px;
    }
`
const TopImg =styled.img`
    width: 350px;
    height: 200px;
    @media screen and (max-width:1024px) {
      width: 800px;
      height: 500px;
    }
    @media screen and (max-width:800px) {
      width: 500px;
    }
    @media screen and (max-width:500px) {
      width: 350px;
      height: 350px;
    }
    
`
const TopText = styled.p`
    color: #fff;
    background-color: rgba(0,0,0,0.5);
    padding: 10px;
    font-size: 18px;
    font-weight: bold;
    position: relative;
    top: -20px;
`
const BottomBox = styled.div`
    display: flex;
    gap: 16px;

    @media screen and (max-width:1024px) {
      flex-direction: column;
      gap: 0;
    }
`
const ShopBest = styled.div`
    display: flex;
    flex-direction: column;
    background-color: #f6f3f2;
    padding: 50px 20px 20px 20px;
    @media screen and (max-width:1024px) {
      width: 800px;
      align-items: center;
    }
    @media screen and (max-width:800px) {
      width: 500px;
    }
    @media screen and (max-width:500px) {
      width: 350px;
    }
`
const BottomItems =styled.div`
    display: flex;
    flex-direction: column;
    width: 200px;
    cursor: pointer;
    position: relative;
    @media screen and (max-width:1024px) {
      width: 800px;
      align-items: center;
      background-color: #f6f3f2;
      padding: 10px;
    }
    @media screen and (max-width:800px) {
      width: 500px;
    }
    @media screen and (max-width:500px) {
      width: 350px;
    }
`
const TextBox = styled.div`
    display: flex;
`
const H3 = styled.h3`
    font-size: 24px;
`
const P2 = styled.p`
    margin : 30px 0;
    font-size: 15px;
    font-weight: bold;
    color: #777;
    line-height: 30px;
`
const Abtn = styled.a`
    text-decoration: none;
    font-weight: bold;
    color: #002c5f;

    &::after{
        content: "";
        display: inline-block;
        width: 13px;
        height: 13px;
        vertical-align: middle;
        background: url("/assets/ico_btn.png") no-repeat;
        background-position: 0 -19px;
        margin-left: 6px;
        font-size: 16px;
    }
`
const BottomNumber = styled.a`
    position: absolute;
    background-color: #007fa8;
    width: 30px;
    height: 30px;
    color: #fff;
    text-align: center;
    font-weight: bold;
    line-height: 30px;
`
const BottomImg = styled.img`
    border: 1px solid #f6f3f2;
    width: 200px;
    height: 200px;
`
const BottomText = styled.p`
    margin: 10px 0;
    line-height: 25px;
    font-size: 14px;
    font-weight: bold;
`
const Price = styled.p`
    font-weight: bold;
    font-size: 24px;
    margin: 0 5px;
`
const Sale = styled.p`
    color: red;
    font-weight: bold;
    position: relative;
    top: 7px;
`
const Span = styled.span`
    display: flex;
    align-items: flex-end;
    position: relative;
    top: -3px;
`
const topitem = [
    {
        id : 1,
        text : "초보세차 레벨 업!",
        img : "assets/shop1.jpg"
    },
    {
        id : 2,
        text : "자동차 필터 종류와 교체시기는?",
        img : "assets/shop2.jpg"
    },
    {
        id : 3,
        text : "캠핑 초보라면 집중!",
        img : "assets/shop3.jpg"
    }
]
const bottomitems = [
    {
        id : 1,
        number : 1,
        img : "assets/shopitem1.jpg",
        text : "[다이슨] DYSON 에어랩 멀티스타일러 컴플리트 롱",
        sale : "8% ↓",
        price : "690,000"
    },
    {
        id : 2,
        number : 2,
        img : "assets/shopitem2.jpg",
        text : "[디테일링] BLDC 차량용 핸디형 무선 청소기 플러스",
        sale : "28% ↓",
        price : "99,000"
    },
    {
        id : 3,
        number : 3,
        img : "assets/shopitem3.jpg",
        text : "[다이슨] DYSON 슈퍼소닉 HD15 블루코퍼 (케이스내장)",
        sale : "13% ↓",
        price : "479,000"
    },
    {
        id : 4,
        number : 4,
        img : "assets/shopitem4.jpg",
        text : "H Genuine 충전케이블",
        price : "240,000"
    },
]
function Shop() {
  return (
    <Wrapper>
        <H1>현대 Shop</H1>
        <P>현대자동차 공식 온라인몰에서 다양한 상품을 확인해보세요.</P>
        <Container>
            <TopBox>
                {topitem.map((items)=>(
                    <TopItems key={items.id}>
                        <TopImg src={items.img}></TopImg>
                        <TopText>{items.text}</TopText>
                    </TopItems>
                ))}
            </TopBox>
            <BottomBox>
                <ShopBest>
                    <H3>현대 Shop Best</H3>
                    <P2>지금 현대Shop에서 <br />
                        인기 있는 상품이에요</P2>
                    <Abtn href='#'>더보기</Abtn>
                </ShopBest>
                {bottomitems.map((bitems)=>(
                    <BottomItems key={bitems.id}>
                        <BottomNumber>{bitems.number}</BottomNumber>
                        <BottomImg src={bitems.img}></BottomImg>
                        <BottomText>{bitems.text}</BottomText>
                        <TextBox>
                        <Sale>{bitems.sale}</Sale>
                        <Price>{bitems.price}</Price>
                        <Span>원</Span>
                        </TextBox>
                    </BottomItems>
                ))}
            </BottomBox>
        </Container>
    </Wrapper>
  )
}

export default Shop