import React, { useState } from 'react'
import styled from 'styled-components'
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Autoplay, Pagination, Navigation, A11y } from 'swiper';
import 'swiper/swiper-bundle.min.css';

SwiperCore.use([Autoplay, Pagination, Navigation, A11y]);

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 100px 0;
    gap: 16px;
    @media screen and (max-width:400px) {
        padding-top: 30px;
    }
`;

const H1 = styled.h1``;

const P = styled.p`
    padding: 30px 0 50px 0;
    color: #777;
    @media screen and (max-width:400px) {
       text-align: center;
    }
`;

const Container = styled.div`
    margin: 0 auto;
    max-width: 1600px;
    @media screen and (max-width:1600px) {
        max-width: 1240px;
    }
    @media screen and (max-width:1240px) {
        max-width: 960px;
    }
    @media screen and (max-width:1024px) {
        max-width: 960px;
    }
    @media screen and (max-width:960px) {
        max-width: 700px;
    }
    @media screen and (max-width:700px) {
        max-width: 400px;
    }
    @media screen and (max-width:400px) {
        max-width: 300px;
    }
`;

const ItemBox = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    cursor: pointer;
`;

const HoverBox = styled.div`
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(0,0,0,0.8);
    display: ${({$hover})=>($hover === "true" ? "block" : "none")};
`;

const HoverText = styled.p`
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    color: #fff;
    font-size: 24px;
    font-weight: bold;
`;

const ImgBox = styled.div`
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
`;

const Img = styled.img`
    width: 100%;
    object-fit: contain;
`;

const TextBox = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 20px;
    gap: 20px;
`;

const Title = styled.h1`
    font-size: 20px;
`;

const Text = styled.p`
    font-size: 13px;
    color: #333;
    font-weight: 500;
`;

const Date = styled.p`
    font-size: 14px;
    color: #333;
    font-weight: 500;
`;

const CustomSwiperContainer = styled.div`
  width: 100%;
  .swiper-container {
    display: flex;
    flex-direction: column-reverse;
  }
  .swiper-slide {
    width: 500px;
  }

  .swiper-pagination{
    z-index: 1000;
    position: static;
    margin-top: 50px;
    @media screen and (max-width:400px){
        margin-top: 20px;
    }
  }
  .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    background-color: #007bff;
  }
  .swiper-button-prev{
    background-color: rgba(0,0,0,0.3);
    color: white;
    width: 50px;
    left: 0;
    top: 35%;
    z-index: 100;
    &::after{
       font-size: 12px;
       font-weight: bold;
    }
  }
  .swiper-button-next{
    background-color: rgba(0,0,0,0.3);
    color: white;
    width: 50px;
    right: 0;
    top: 35%;
    z-index: 100;
    &::after{
       font-size: 12px;
       font-weight: bold;
    }
  }
`;

const items = [
    {
        id : 1,
        img : "assets/events1.jpg",
        title : "7월 신차 구매 혜택",
        text : "현대자동차 전용카드 7월 신차 구매 혜택",
        date : "2024-07-01 ~ 2024-07-31"
    },
    {
        id : 2,
        img : "assets/events2.png",
        title : "CASPER Electric in Busan",
        text : "부산역에서 캐스퍼 일렉트릭을 만나보세요!",
        date : "2024-06-28 ~ 2024-07-07"
    },
    {
        id : 3,
        img : "assets/events3.jpg",
        title : "7월 포터 Ⅱ Electric 프로모션",
        text : "노란우산공제회원 대상 특별 할인 프로모션을 만나보세요.",
        date : "2024-07-01 ~ 2024-07-31"
    },
    {
        id : 4,
        img : "assets/events4.jpg",
        title : "캐스퍼 일렉트릭 AI그리기대회",
        text : "AI로 완성하는 나만의 캐스퍼 일렉트릭",
        date : "2024-07-01 ~ 2024-07-21"
    },
    {
        id : 5,
        img : "assets/events5.jpg",
        title : "2024 부산모빌리티쇼 현대자동차관 살펴보기",
        text : "현대자동차가 그리는 새로운 일상과 내일,그리고 그 너머의 지속가능한 미래를 향해",
        date : "2024-06-27 ~ 2024-07-07"
    },
    {
        id : 6,
        img : "assets/events6.jpg",
        title : "7월 싼타페와 함께하는 호캉스 이벤트",
        text : "국민 패밀리 SUV 싼타페 계약하고, 우리가족 호캉스 가자!",
        date : "2024-07-01 ~ 2024-07-31"
    },
];

function Events() {
  const [hoveredIndex, setHoveredIndex] = useState(null);

  return (
    <Wrapper>
        <H1>Events</H1>
        <P>고객님을 위한 스페셜한 이벤트를 통해 즐거운 행운과 경품을 만나보세요.</P>
        <Container>
        <CustomSwiperContainer>
            <Swiper
                modules={[Navigation, Pagination, A11y, Autoplay]}
                spaceBetween={50}
                slidesPerView={3}
                slidesPerGroup={3}
                breakpoints={{
                    1240: {
                        slidesPerView: 3,
                        slidesPerGroup: 3
                    },
                    960: {
                        slidesPerView: 2,
                        slidesPerGroup: 2
                    },
                    0: {
                        slidesPerView: 1,
                        slidesPerGroup: 1
                    }
                }}
                style={{
                  '--swiper-navigation-color': '#333',
                  '--swiper-pagination-color': '#007bff',
                }}
                pagination={{ clickable: true }}
                navigation={true}
                loop={true}
            >
                
                {items.map((item, index)=>(
                    <SwiperSlide key={item.id}>
                        <ItemBox>
                        <ImgBox 
                            onMouseEnter={() => setHoveredIndex(index)} 
                            onMouseLeave={() => setHoveredIndex(null)}
                        >
                            <Img src={item.img}></Img>
                            <HoverBox $hover={hoveredIndex === index ? "true" : "false"}>
                                <HoverText>자세히보기 +</HoverText>
                            </HoverBox>
                        </ImgBox>
                        <TextBox>
                            <Title>{item.title}</Title>
                            <Text>{item.text}</Text>
                            <Date>{item.date}</Date>
                        </TextBox>
                        </ItemBox>
                    </SwiperSlide>
                ))}
                
            </Swiper>
            </CustomSwiperContainer>
        </Container>
    </Wrapper>
  )
}

export default Events;
