import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 100px 0;
    gap: 16px;
    @media screen and (max-width:1024px){
        padding: 30px 0;
    }
    @media screen and (max-width:700px){
        width: 90%;
        margin: 0 auto;
    }
`
const H1 = styled.h1`

`
const P = styled.p`
    padding: 30px 0 50px 0;
    color: #777;
    @media screen and (max-width:400px) {
       text-align: center;
    }
`

const Container =styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    height: 445px;
    @media screen and (max-width:1024px){
        flex-direction: column;
        height: auto;
        position: relative;
    }
    
`
const LeftBox =styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: #f6f3f2;
    width: 35%;
    padding: 50px;
    height: 100%;
    @media screen and (max-width:1024px){
        width: 100%;
        position: absolute;
        bottom: -305px;
        height: 70%;
    }
    @media screen and (max-width:700px){
        bottom: -245px;
        padding: 30px;
    }
    @media screen and (max-width:600px){
        bottom: -205px;
        padding: 10px;
    }
    @media screen and (max-width:500px){
        bottom: -255px;
        height: 100%;
    }
    @media screen and (max-width:400px){
        bottom: -195px;
        
    }
`
const TitleBox = styled.div`
    display: flex;
`
const H2 = styled.h2`
    font-size: 30px;
    @media screen and (max-width:400px){
        font-size: 27px;
    }
`
const Span = styled.span`
   font-size: 30px;
   margin-left: 5px;
`
const P2 = styled.p`
    font-size: 16px;
    margin-top: 13px;
    line-height: 30px;
    font-weight: bold;
    @media screen and (max-width:600px){
        line-height: 20px;
    }
    @media screen and (max-width:400px){
        font-size: 10px;
    }
`
const Button = styled.button`
    width: 120px;
    height: 40px;
    color: white;
    background-color: #002c5f;
    font-size: 15px;
    font-weight: 500;
    border: 0;
    cursor: pointer;
    margin-top: 50px;
    @media screen and (max-width:1024px){
        width: 100%;
    }
    @media screen and (max-width:400px){
        margin-top: 30px;
    }
`
const RightBox = styled.div`
    width: 65%;
    height: 100%;
    @media screen and (max-width:1024px){
        width: 100%;
    }
    
`
const Img =styled.img`
    @media screen and (max-width:700px){
        width: 100%;
    }
`
function Trendy() {
  return (
    <Wrapper>
        <H1>Trendy Hyundai</H1>
        <P>현대자동차와 현대닷컴 소식을 이제 트렌드 리포트로 만나보세요.</P>
        <Container>
            <LeftBox>
                <TitleBox>
                <H2>Trendy</H2> <Span>Hyundai</Span>
                </TitleBox>
                <P2>모델 트렌드부터 추천 액세서리까지<br />
                    매월 달라지는 소식을 기대해 주세요.</P2>
                <Button>자세히 보기</Button>
            </LeftBox>
            <RightBox>
                <Img src='assets/trendy.jpg'></Img>
            </RightBox>
        </Container>
    </Wrapper>
  )
}

export default Trendy