import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;

`
const Img = styled.img`
    width: 166px;
    height: 30px;

    object-fit: cover;
`

function Logo() {
  return (
    <Wrapper>
        <Img src='assets/logo-hyundai.png'></Img>
    </Wrapper>
  )
}

export default Logo