import React, { useState, useEffect } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Autoplay, Pagination, Navigation, A11y } from 'swiper';
import 'swiper/swiper-bundle.min.css';
import styled from 'styled-components';

SwiperCore.use([Autoplay, Pagination, Navigation, A11y]);

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 100px 0;
  gap: 16px;
  @media screen and (max-width:400px){
    padding: 0;
  }
`;

const H1 = styled.h1``;

const P = styled.p`
  padding: 30px 0 50px 0;
  color: #777;
  @media screen and (max-width: 400px) {
    text-align: center;
  }
`;

const Container = styled.div`
  display: flex;
  gap: 30px;
`;

const ItemBox = styled.div`
  position: relative;
  cursor: pointer;
  overflow: hidden;
  &:hover > div {
    display: flex;
  }
  &:hover > img {
    transform: scale(1.2);
  }
 
`;

const Img = styled.img`
    width: 100%;
  transition: 0.4s;
  object-fit: contain;
`;

const TextBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  text-align: center;
  padding: 10px;
  display: none;
`;

const H2 = styled.h2``;

const P2 = styled.p`
  font-size: 14px;
  line-height: 30px;
  font-weight: bold;
`;

const items = [
  {
    id: 1,
    img: 'assets/brand1.jpg',
    imgMobile: 'assets/brand1_m.jpg',
    title: '파트너십',
    text: 'ZERO1EN:혁신적인 기술이 시작 되는 곳',
  },
  {
    id: 2,
    img: 'assets/brand2.jpg',
    imgMobile: 'assets/brand2_m.jpg',
    title: '디자인',
    text: 'Dogbility – 반려견의 이동의 자유를 위한 모빌리티',
  },
  {
    id: 3,
    img: 'assets/brand3.jpg',
    imgMobile: 'assets/brand3_m.jpg',
    title: '친환경 프로젝트',
    text: '2024 지구의 날 – 올해도 헬시 씨즈(Healthy Seas)와 함께',
  },
  {
    id: 4,
    img: 'assets/brand4.jpg',
    imgMobile: 'assets/brand4_m.jpg',
    title: '탄소중립 비전',
    text: 'The last safety feature',
  },
];

const CustomSwiperContainer = styled.div`
  width: 100%;
  .swiper-container {
    display: flex;
    flex-direction: column-reverse;
  }
  .swiper-pagination{
    z-index: 1000;
    position: static;

  }
  .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    background-color: #007bff;
    
  }
  @media screen and (max-width: 960px) {
    width: 90%;
  }
`;

function Brand() {
  const [isMobile, setIsMobile] = useState(window.innerWidth < 1024);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 1024);
    };

    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <Wrapper>
      <H1>Brand</H1>
      <P>지속가능한 미래로의 연결을 추구하는 현대자동차의 브랜드 캠페인을 만나보세요.</P>
      {isMobile ? (
        <CustomSwiperContainer>
          <Swiper
            modules={[Navigation, Pagination, A11y, Autoplay]}
            spaceBetween={30}
            slidesPerView={2}
            breakpoints={{
              1024: {
                slidesPerView: 1,
                slidesPerGroup: 1,
              },
              0: {
                slidesPerView: 2,
                slidesPerGroup: 2,
              },
            }}
            style={{
              '--swiper-navigation-color': '#333',
              '--swiper-pagination-color': '#007bff',
            }}
            pagination={{ clickable: true }}
            loop={true}
          >
            {items.map((item) => (
              <SwiperSlide key={item.id}>
                <ItemBox>
                  <Img src={isMobile ? item.imgMobile : item.img} />
                  <TextBox>
                    <H2>{item.title}</H2>
                    <P2>{item.text}</P2>
                  </TextBox>
                </ItemBox>
              </SwiperSlide>
            ))}
          </Swiper>
        </CustomSwiperContainer>
      ) : (
        <Container>
          {items.map((item) => (
            <ItemBox key={item.id}>
              <Img src={item.img} />
              <TextBox>
                <H2>{item.title}</H2>
                <P2>{item.text}</P2>
              </TextBox>
            </ItemBox>
          ))}
        </Container>
      )}
    </Wrapper>
  );
}

export default Brand;
