import React from 'react'
import styled from 'styled-components'
import Utill from './Utill'

const Wrapper = styled.div`
    width: 75%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width:960px) {
        justify-content: flex-end;
    }
`

const Ul = styled.ul`
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;

    gap: 40px;

    @media screen and (max-width:960px) {
        display: none;
    }
`
const Li = styled.li`
    font-size: 15px;
`
const A = styled.a`
    display: block;
    text-decoration: none;
    color: black;
`
const menu = ['모델','구매/이벤트','서비스/멤버십','디지털/고객지원','브랜드']
const menuItem = menu.map((list) => 
    <Li key={list}><A href='#'>{list}</A></Li>
);

function Navigation() {
  return (
    <Wrapper>
        <Ul>
            {menuItem}
            <Li><A href='#'><img src="assets/hyundai_shop_logo.png" alt="로고" /></A></Li>
        </Ul>
        <Utill />
    </Wrapper>
  )
}

export default Navigation