import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f6f3f2;
    width: 100%;
    height: 300px;
    padding: 30px 10px 0 10px;
    overflow: hidden;
    @media screen and (max-width:760px){
        height: auto;
        padding: 0;
    }
    @media screen and (max-width:400px){
        padding-top: 20px;
        margin-top: 50px;
    }
`
const Container = styled.div`
    display: flex;
    flex-direction: column;
    width: 1200px;
    border: 1px solid #e4dcd3;
    background-color: #fff;
    border-radius: 20px 20px 0 0;
    padding: 20px;
    @media screen and (max-width:760px){
        padding: 0;
        width: 90%;
    }
`
const TitleBox = styled.div`
    display: flex;
    padding: 20px 50px 0 50px;
    @media screen and (max-width:760px){
        padding: 20px 0 0 10px;
    }
`
const P = styled.p`
    font-weight: bold;
`
const ItemBox = styled.div`
    display: flex;
    align-items: flex-start;
    padding: 10px 30px 30px 30px;
    @media screen and (max-width:760px){
        flex-direction: column;
        padding: 20px 0 0 10px;
    }
`
const ItemInner = styled.div`
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    padding: 20px;
    border-right: 1px solid #e4dcd3;

    @media screen and (max-width:760px){
        padding: 5px;
    }
    &:last-child{
      border: none;
    }
    @media screen and (max-width:760px){
        border-right: none;
        border-bottom: 1px solid #e4dcd3;
    }
    
`
const ItemTitle = styled.span`
    color: #002c5f;
    font-weight: bold;
    width: 100%;
    margin-bottom: 10px;
`
const ItemList = styled.a`
  display: flex;
  align-items: center;
  width: 50%;
  margin: 6px 0;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
  color: #333;
`

const Icon = styled.span`
        display: inline-block;
        width: 13px;
        height: 13px;
        vertical-align: middle;
        background: url("assets/ico_btn.png") no-repeat;
        background-position: 0 -19px;
        margin-left: 6px;
        font-size: 16px;
`

const items = [
  {
    id: 1,
    title: "기업정보",
    item: ["기업이념", "연혁", "주요관계사", "CI", "수상내역", "인재채용"]
  },
  {
    id: 2,
    title: "IR정보",
    item: ["IR정보", "배당조회"]
  },
  {
    id: 3,
    title: "지속가능 경영",
    item: ["지속가능 경영", "CSV소개", "지속가능성보고서", "사회공헌백서"]
  }
]

const iconItems = ["인재채용", "IR정보", "배당조회"];

function Enterprise() {
  return (
    <Wrapper>
      <Container>
        <TitleBox>
            <P>기업정보 · IR정보 · 지속가능경영</P>
        </TitleBox>
        <ItemBox>
            {items.map((item) => (
              <ItemInner key={item.id}>
                  <ItemTitle>{item.title}</ItemTitle>
                  {item.item.map((a, index) => a && (
                    <ItemList href='#' key={index}>
                      {a}
                      {iconItems.includes(a) && <Icon href='#'/>}
                    </ItemList>
                  ))}
              </ItemInner>
            ))}
        </ItemBox>
      </Container>
    </Wrapper>
  )
}

export default Enterprise
