import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    height: 200px;
    background-color: #1c1b1b;
    @media screen and (max-width:760px){
        height: auto;
    }
`
const Container = styled.div`
    display: flex;
    align-items: flex-start;
    width: 1200px;
    border-bottom: 1px solid #333;
    padding-bottom: 30px;
    @media screen and (max-width:1024px){
        position: relative;
    }
    
`
const ImgBox = styled.div`
    width: 150px;
    @media screen and (max-width:760px){
        position: absolute;
        bottom: -50px;
        left: 10px;
    }
`
const Img = styled.img`
    
`
const TextBox = styled.div`
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        gap: 8px;
        padding-right: 100px;
        @media screen and (max-width:760px){
            padding-right: 0;
    }
`
const Ul = styled.ul`
    display: flex;

    &:nth-child(1)>Li:nth-child(3){
        font-weight: bold;
        color: #fff;
    }
    @media screen and (max-width:760px){
        flex-wrap: wrap;
        width: 100%;
    }
`
const Li = styled.li`
    padding: 0 10px;
    color: #999;
    list-style: none;
    &:after{
        content: "";
        border-right: 1px solid #999;
        font-size: 10px;
        padding: 0 10px;
        width: 1px;
        @media screen and (max-width:760px){
        width: 50%;
        border: none;
    }
    }
    &:last-child:after {
        border: none;
        
    }
    @media screen and (max-width:760px){
        width: 50%;
        padding: 20px
    }
`
const A = styled.a`
    font-size: 14px;
    text-decoration: none;
  
`
const P = styled.p`
    font-size: 14px;
    color: #999;
    padding: 0 10px;
    @media screen and (max-width:760px){
        padding: 0 20px;
    }
`
const P2 = styled.p`
    font-size: 12px;
    color: #999;
    padding: 0 10px;

    @media screen and (max-width:760px){
        position: absolute;
        bottom: -40px;
        left: 70px;
    }
    @media screen and (max-width:400px){
        bottom: -50px;
    }
`
const SnsBox = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    gap: 8px;
    @media screen and (max-width:1024px){
        position: absolute;
        right: 10px;
        bottom: 10px;
    }
    @media screen and (max-width:760px){
        display: none;
    }
`
const IconList = styled.ul`
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 6px;
    
`
const Icons = styled.li`
    list-style: none;
    background-size: 100%;
    background-repeat: no-repeat;
    &:nth-child(1){
        background-position: 0 -30px;
    }
    &:nth-child(2){
        background-position: 0 -150px;
    }
    &:nth-child(4){
        background-position: 0 -120px;
    }
    &:nth-child(5){
        background-position: 0 -90px;
    }
    &:nth-child(6){
        background-position: 0 -180px;
    }
    &:nth-child(7){
        background-position: 0 -210px;
    }
`
const A2 = styled.a`
    display: block;
    width: 30px;
    height: 32px;
`
const Family = styled.select`
    width: 100%;
    padding: 5px 10px;
    background-color: #1c1b1b;
    color: #999;
`
const Option = styled.option`
    
`
const texts = [
    {
        id : 1,
        text : ["모바일 App","저작권안내","개인정보 처리방침","이용약관","프라이버시 센터"]
    },
    {
        id : 2,
        text : ["윤리경영 사이버 감사실","영상정보처리기기 운영관리방침","사이트맵"]
    }
]
const icon = [
    {
        id : 1,
        url : "/assets/ico_social.png"
    },
    {
        id : 2,
        url : "/assets/ico_social.png"
    },
    {
        id : 3,
        url : "/assets/ico_social.png"
    },
    {
        id : 4,
        url : "/assets/ico_social.png"
    },
    {
        id : 5,
        url : "/assets/ico_social.png"
    },
    {
        id : 6,
        url : "/assets/ico_social.png"
    },
    {
        id : 7,
        url : "/assets/ico_social.png"
    },
]
const family = [
    {
        id : 1,
        site : "Family Site"
    },
    {
        id : 2,
        site : "현대자동차그룹"
    },
    {
        id : 3,
        site : "제네시스닷컴"
    },
    {
        id : 4,
        site : "캐스퍼 온라인"
    },
    {
        id : 5,
        site : "N 브랜드"
    },
    {
        id : 6,
        site : "인재채용"
    },
    {
        id : 7,
        site : "경제산업연구센터"
    },
    {
        id : 8,
        site : "현대 모터스튜디오"
    },
    {
        id : 9,
        site : "키즈현대"
    },
    {
        id : 10,
        site : "동반성장 사이트"
    },
    {
        id : 11,
        site : "현대 드라이빙 아카데미"
    },
    {
        id : 12,
        site : "현대 컬렉션 온라인샵"
    },
    {
        id : 13,
        site : "전북 현대모터스 축구단"
    },

]
function TopFooter() {
  return (
    <Wrapper>
        <Container>
            <ImgBox>
                <Img src='assets/logo_footer.png'></Img>
            </ImgBox>
            <TextBox>
                {texts.map((item)=>(
                    <Ul key={item.id}>
                            {item.text.map((i)=>(
                                 <Li key={i}><A>{i}</A></Li>
                            ))}
                    </Ul> 
                ))}
                <P>고객센터 : 080-600-6000</P>
                <P2>COPYRIGHT ⓒ HYUNDAI MOTOR COMPANY. ALL RIGHTS RESERVED.</P2>
            </TextBox>
            <SnsBox>
                <IconList>
                {icon.map((icon)=>(
                    <Icons key={icon.id} style={{ backgroundImage: `url(${icon.url})` }}>
                        <A2 href='#'></A2>
                    </Icons>
                ))}
                </IconList>
                <Family>
                    {family.map((item)=>(
                        <Option key={item.id}>{item.site}</Option>
                    ))}
                </Family>
            </SnsBox>
        </Container>
    </Wrapper>
  )
}

export default TopFooter