import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;

    padding: 200px 0;
    gap: 16px;
    @media screen and (max-width:500px){
        padding: 100px 0 0 0;
    }
`
const H1 = styled.h1`
    font-size: 40px;
`
const P = styled.p`
    @media screen and (max-width:500px){
        font-size: 12px;
        margin-bottom: 50px;
    }
`
const ItemBox = styled.div`
    display: flex;

    @media screen and (max-width:960px){
        flex-direction: column;
    }
`
const LeftBox = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
    @media screen and (max-width:960px){
        margin-right: 0;
    }
`
const RightBox = styled.div`
    display: flex;
    position: relative;
    padding: 20px;
    gap: 8px;
    @media screen and (max-width:960px){
        flex-direction: column;
    }
`
const RightItem = styled.div`
    display: flex;
    flex-flow: column;
    align-items: flex-start;
    justify-content: flex-end;

    gap: 8px;
    @media screen and (max-width:960px){
        margin-top: 20px;
        justify-content: flex-start;
    }
    
`
const Img = styled.img`
    @media screen and (max-width:500px){
        height: 250px;
    }
`
const Img2 = styled.img`
    height: 180px;
    @media screen and (max-width:960px){
        height: auto;
    }
    @media screen and (max-width:500px){
        height: 250px;
    }
`
const Ul = styled.ul`
    position: absolute;
    top: 30px;
    display: flex;
    gap: 10px;
    @media screen and (max-width:960px){
        top: -420px;
        right: 50px;
    }
    @media screen and (max-width:500px){
        top: -300px;
        right: 90px;
    }
`
const Li = styled.li`
    list-style: none;
    text-align: center;
`
const A = styled.a`
    font-weight: bold;
`
const H2 = styled.h2`
    position: absolute;
    font-size: 32px;
    top: 70px;
    @media screen and (max-width:960px){
        top: 0;
        font-size: 20px;
    }
`
const Button = styled.button`
    border: none;
    background-color: transparent;
    padding: 6px;
    cursor: pointer;
    &:active {
        color: #007fa8;
    }
`
function BestCar() {
  return (
    <Wrapper>
        <H1>Model</H1>
        <P>최근 한 달 동안 고객님들이 가장 많이 구매하신 모델입니다.</P>
        <ItemBox>
            <LeftBox>
                <Img src='assets/MX04_EXT.jpg'></Img>
            </LeftBox>
            <RightBox>
                <RightItem>
                    <Ul>
                        <Li><Button> 전체</Button> |</Li>
                        <Li><Button> 2~30대</Button> |</Li>
                        <Li><Button> 40대</Button> |</Li>
                        <Li><Button> 50대</Button></Li>
                    </Ul>
                    <H2>1위. 디 올 뉴 싼타페 Hybrid</H2>
                    <Img2 src='assets/NX10_EXT.jpg'></Img2>
                    <A>2위. 더 뉴 투싼 Hybrid</A>
                </RightItem>
                <RightItem>
                    <Img2 src='assets/CN12_EXT.jpg'></Img2>
                    <A>3위. 더 뉴 아반떼</A>
                </RightItem>
            </RightBox>
        </ItemBox>
    </Wrapper> 
  )
}

export default BestCar